<template>
	<view class="body">
		<!-- 头部模块 -->
		<view class="head">
			<view class="word" @click="toNewWord">
				<img src="../../static/langStatic/wordBook.png">
				<text>单词本</text>
			</view>
			<view class="head_title">Text4，2008</view>
			<view class="difficulty">
				<view>难度</view>
				<u-icon name="star-fill" color="#ffd75c" size="28rpx"></u-icon>
				<u-icon name="star-fill" color="#ffd75c" size="28rpx"></u-icon>
				<u-icon name="star-fill" color="#e3e3e3" size="28rpx"></u-icon>
			</view>
		</view>
		<!-- 句子 -->
		<view class="sentence" v-html="data.subject"></view>
		<!-- 图片 -->
		<image class="picture" :src="data.img" @click="openImg"></image>
		<!-- 单元 -->
		<view class="title">
			<view class="number">{{data.index}}</view>
			<view class="text">{{data.title}}</view>
		</view>
		<!-- 答案 -->
		<view v-if="isSee==false" class="answer">
			<textarea name="" id="" v-model="answer" cols="30" rows="10"></textarea>
			<button @click="seeResolution">查看解析</button>
		</view>
		<!-- 答案 -->
		<view v-else class="answer">
			<textarea v-if="data.option_answer" name="" id="" v-model="data.option_answer.answer" cols="30" rows="10" :disabled="true"></textarea>
			<textarea v-else name="" id="" v-model="answer" cols="30" rows="10" :disabled="true"></textarea>
			<button>查看解析</button>
		</view>
		
		<!-- 答案解析 -->
		<view v-show="isSee" class="answerAnalysis">
			<view class="title">答案解析</view>
			<view class="content" v-html="data.analysis"></view>
		</view>
		<!-- 下一步 -->
		<button @click="toSummary">完成</button>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { subject, checkAnswer } from '@/common/http.api.js';
	export default{
		data() {
			return {
				answer:'', //答案
				isSee:false, //解析显示状态
				data:{
					answer:'几位历史学家的著作揭示了美国早期领袖所做出的道德妥协以及建国初期的脆弱性。'
				}
			}
		},
		onLoad() {
			this.videoContext = uni.createVideoContext('videos')
			this.token = store.state.vuex_token 
			let id = store.state.subject_id //大题ID
			subject({token:this.token,subject_id:id,order:2}).then(res=>{
				console.log(res)
				if(res.data.data.option_answer){
					this.isSee = true
				}
				this.data = res.data.data
				console.log(this.data)
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 查看解析
			seeResolution() {
				if(this.answer){
					this.isSee = true
					let answers = {
						answer:this.answer
					}
					let id = store.state.subject_id //大题ID
					let answer = JSON.stringify(answers)
					console.log("-----")
					console.log(answers)
					console.log(answer)
					let params = {
						answer: answer,
						answer_type:this.data.type,
						is_end:this.data.is_end,
						option_id:this.data.id,
						subject_id:id,
						token:this.token
					}
					console.log(params)
					checkAnswer({...params}).then(res=>{
						console.log(res)
					}).catch(err=>{
						console.log(err)
					})
				}else{
					this.$u.toast("先尝试一下吧~")
				}
			},
			// 去总结
			toSummary() {
				if(this.isSee==true){
					uni.navigateTo({
						url: '/pages/index/summary'
					})
				}else{
					this.$u.toast("做完再走~")
				}
			},
			// 去单词本
			toNewWord() {
				uni.navigateTo({
					url: '/pages/index/newWordBook'
				})
			},
			// 预览图片
			openImg() {
				uni.previewImage({
					urls:[this.data.img],
				})
			}
		}
	}
</script>

<style lang="scss">
	.body{
		padding-bottom: 40rpx;
		button{
			background: linear-gradient(to right, #ffd23f, #fca51d);
			color: #fff;
			font-size: 32rpx;
			line-height: 88rpx;
			border-radius: 100rpx;
			width: 80%;
		}
		.picture{
			margin: 30rpx;
			height: 200rpx;
		}
		.answer{
			margin: 30rpx;
			textarea{
				border: 1rpx solid #e1e1e1;
				border-radius: 10rpx;
				font-size: 28rpx;
				padding: 20rpx;
				margin-bottom: 30rpx;
				line-height: 58rpx;
				height: 200rpx
			}
			button{
				padding: 0;
				color: #ffd942;
				width: 150rpx;
				border: 1px solid #ffd942;
				border-radius: 12rpx;
				font-size: 28rpx;
				line-height: 60rpx;
				background-color: transparent;
				background: none;
				margin-bottom: 40rpx;
			}
		}
		.head{
			padding: 0 20rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.word{
				width: 150rpx;
				position: relative;
				background-color: #fff4c7;
				color: #fcaa20;
				text-align: center;
				font-size: 28rpx;
				line-height: 70rpx;
				border-radius: 100rpx;
				padding-left: 30rpx;
				img{
					position: absolute;
					left: 26rpx;
					top: 16rpx;
					width: 30rpx;
					height: 40rpx;
				}
			}
			.difficulty{
				display: flex;
				view{
					color: #b2b2b2;
					font-size: 28rpx;
					margin-right: 10rpx;
				}
			}
		}
		.sentence{
			margin: 40rpx 30rpx;
			line-height: 68rpx;
		}
		.title{
			margin: 40rpx 30rpx;
			display: flex;
			margin-bottom: 30rpx;
			.number{
				background-color: #797979;
				color: #fff;
				width: 36rpx;
				line-height: 36rpx;
				height: 36rpx;
				text-align: center;
				margin-right: 20rpx;
			}
			.text{
				line-height: 36rpx;
				font-size: 30rpx;
				font-weight: 500;
				letter-spacing: 2rpx;
			}
		}
		.answerAnalysis{
			margin: 30rpx 40rpx;
			.title{
				padding-left: 30rpx;
				background-image: url(../../static/point.png);
				background-repeat: no-repeat;
				background-size: 20rpx;
				margin: 0;
				font-size: 28rpx;
				color: #828282;
				background-position: left center; 
			}
			.content{
				margin: 30rpx 0;
			}
		}
	}
</style>